<template>
  <div class="luck-lottery" ref="luckdraw" :style="{width:luckWidth, height:luckheight}">
    <div class="lucktable" :style="{transform: rotateAngle, transition: rotateTransition}">
      <canvas id="canvas" ref="canvas">
        
      </canvas>
      <div class="prize">
        <div v-for="(item, index) in prizeList" class="item" :style="getRotateAngle(index)" :key="index">
          <slot name="item" :item="item"></slot>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    name:'luck-lottery',
    mounted() {
      this.init();
    },
    props: {
      luckWidth: {
        required: true
      },
      luckheight: {
        required: true
      },
      prizeList: {
        required: true
      },
      turnsNumber: {
        default: 5
      },
      styleOpt: {
        default: () => {
          return {
            // ,,
            prizeBgColors: ['rgb(255, 231, 149)','rgb(255, 247, 223)','rgb(255, 231, 149)','rgb(255, 247, 223)','rgb(255, 231, 149)','rgb(255, 247, 223)'],
            // ,,
            borderColor: '#ff9800',
          }
        }
      },
      turnsTime: {
        // ()  
        default: 5
      }
    },
    data() {
      return {
        winningPrize: 0,
        // 
        startRotateDegree: 0,
        // ,2
        rotateAngle: 0,//`rotate(30deg)`
        rotateTransition: '',
      }
    },
    methods: {
      // index
      getRotateAngle(index) {
        const angle = 360 / this.prizeList.length * index + (180 / this.prizeList.length);
        return {
          transform: `rotate(${angle}deg)`
        };
      },
      // canvas
      init() {
        const data = this.styleOpt;
        const prizeNum = this.prizeList.length;
        const {prizeBgColors, borderColor} = data;
        // 
        const canvas = this.$refs.canvas;
        const ctx = canvas.getContext("2d");
        const canvasW = this.$refs.canvas.width = this.$refs.luckdraw.clientWidth; // 
        const canvasH = this.$refs.canvas.height = this.$refs.luckdraw.clientHeight; // 
        // translate (0,0) 
        ctx.translate(0, canvasH);
        // rotate
        ctx.rotate(-90 * Math.PI / 180);
        // ,
        const outRadius = canvasW / 2 - 1;
        // 
        // const innerRadius = 0;
        const baseAngle = Math.PI * 2 / prizeNum; // 
        ctx.clearRect(0, 0, canvasW, canvasH); //
        ctx.strokeStyle = borderColor; // 
        for (let index = 0; index < prizeNum; index++) {
          const angle = index * baseAngle;
          ctx.fillStyle = prizeBgColors[index]; //
          ctx.beginPath(); //
          ctx.moveTo(canvasW * 0.5, canvasH * 0.5);
          // arc(x,y,radius,startAngle,endAngle,anticlockwise)
          ctx.arc(canvasW * 0.5, canvasH * 0.5, outRadius, angle, angle + baseAngle, false);
          // ctx.arc(canvasW * 0.5, canvasH * 0.5, innerRadius, angle + baseAngle, angle, true);
          ctx.stroke();
          ctx.fill();
          ctx.save();
        }
      },
      // 
      rotate(index) {
        const turnsTime = this.turnsTime;
        // 
        const rotateAngle = this.startRotateDegree + this.turnsNumber * 360
        // 
        + 360 - (180 / this.prizeList.length + 360 / this.prizeList.length * index)
        // 
        - this.startRotateDegree % 360;
        this.startRotateDegree = rotateAngle;
        console.log(rotateAngle);
        this.rotateAngle = `rotate(${rotateAngle}deg)`;
        this.rotateTransition = `transform ${turnsTime}s cubic-bezier(0.250, 0.460, 0.455, 0.995)`;
        setTimeout(() => {
          this.$emit('endTurns');
        }, turnsTime * 1000 + 500)
      },
    },
}
</script>

<style lang="scss" scoped>
.luck-lottery{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  // transform: translateZ(0);
  .lucktable {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .prize {
    position: absolute;
    left: 25%;
    top: 0;
    width: 50%;
    height: 50%;
    .item {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      transform-origin: center bottom;
    }
  }
}
</style>